import React from 'react'
import { Form, Input } from 'antd'

const dummy = {
    isLoggedIn: true
}

const Home = () => {
    return (
        <>
            <div>
                {dummy.isLoggedIn && <Form encType="multipart/form-data">
                    <Input.TextArea maxLength={140}></Input.TextArea>
                </Form>}
            </div>
        </>
    )
}

export default Home